<template>
    <div>
        <div class="login">
        <label for="username">账号</label><input id="username" type="text" v-model='username' placeholder="输入账号" autofocus='autofocus'><br/>
        <label for="psw">密码</label><input id="psw" type="password" v-model='usrpsw' placeholder="输入密码" autocomplete="newpassword"><br/>
        <button @click='login' class="btn btn-primary">登陆</button>
        </div>       
    </div>
</template>
<script>
import Ajaxservice from "@/ajax/ajax-service";
export default {
  name: "login",
  data() {
    return {
      that: this,
      username: "",
      usrpsw: "",
      data: {
        name: "",
        pwd: ""
      }
    };
  },
  methods: {
    login() {
      console.log(this.$router);
      this.data = {
        name: this.username,
        pwd: this.usrpsw
      };
      if (!this.username || !this.usrpsw) {
        alert("账号密码格式不正确！");
        return;
      }
      let formdata = new URLSearchParams();
      formdata.append('name',this.username);
      formdata.append('pwd',this.usrpsw);
      Ajaxservice.login(formdata)
        .then(res => {
          if (res.data.code === 0) {
            this.$router.push("/backstage");
          }
        })
        .catch(res => {
          console.log(res);
        });
    }
  }
};
</script>

<style scoped>
.login {
  padding: 10% 0;
  width: 50%;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  border: 1px solid;
  border-radius: 3%;
}
input {
  margin-left: 1rem;
  border: 1px solid;
}
button {
  width: 70%;
  margin-top: 2rem;
}
</style>



